import React, { forwardRef, useState } from "react";
import styled, { keyframes } from "styled-components";

import { ButtonBottomRight } from "../Button";

type Props = {
  readonly src: string;
  readonly isVisible: boolean;
  readonly onEnded: () => void;
  readonly onPlay?: () => void;
};

type StyleProps = {
  readonly isVisible: boolean;
  readonly hasBlendMode: boolean;
};

type OverlayProps = {
  readonly isVisible: boolean;
};

const fadeIn = keyframes`
  0% {
    opacity: 0;
  }
  
  100% {
    opacity: 1;
  }
`;

const Overlay = styled.div<OverlayProps>`
  transition: 0.5s opacity linear;
  opacity: ${({ isVisible }: OverlayProps) => (isVisible ? 1 : 0)};
  background-color: black;
  position: fixed;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
`;

const StyledVideo = styled.video<StyleProps>`
  transition: 0.5s opacity linear;
  animation: ${fadeIn} 2s;
  object-fit: contain;
  object-position: center;
  width: 100vw;
  height: 100vh;
  position: fixed;

  /* Blend mode hides initial black overlay and smoothens transition */
  mix-blend-mode: ${({ hasBlendMode }: StyleProps) =>
    hasBlendMode ? "screen" : "normal"};
  opacity: ${({ isVisible }: StyleProps) => (isVisible ? 1 : 0)};
  pointer-events: none;
`;

export const Video = forwardRef<HTMLVideoElement, Props>(
  ({ onEnded, onPlay, isVisible, src }, ref) => {
    const [shouldBlend, setShouldBlend] = useState(true);
    const [isVideoVisible, setIsVideoVisible] = useState(false);
    const [shouldShowBlackFadeIn, setShouldShowBlackFadeIn] = useState(false);

    // Add black overlay to simulate fade to/from black while keeping image in the background
    const fadeOutVideo = () => {
      setIsVideoVisible(false);
      setShouldShowBlackFadeIn(true);
      setTimeout(() => {
        setShouldShowBlackFadeIn(false);
      }, 750);
      setTimeout(() => {
        onEnded();
      }, 1000);
    };

    React.useEffect(() => {
      // Hide initial blend mode after fade in
      setIsVideoVisible(isVisible);
      if (isVisible) {
        setTimeout(() => {
          setShouldBlend(false);
        }, 1500);
      }
    }, [isVisible]);

    return (
      <>
        <StyledVideo
          ref={ref}
          preload="auto"
          onEnded={onEnded}
          onPlay={onPlay}
          isVisible={isVideoVisible}
          hasBlendMode={shouldBlend}
        >
          <source src={src} />
        </StyledVideo>
        {isVideoVisible && (
          <ButtonBottomRight onClick={fadeOutVideo}>
            Skip intro
          </ButtonBottomRight>
        )}
        <Overlay isVisible={shouldShowBlackFadeIn} />
      </>
    );
  }
);
